import { makeStyles } from '@material-ui/styles';

const useStyles = makeStyles({
  tableRow: {
    height: '20px',
  },
  tableRowCell: {
    fontWeight: 'normal',
    border: '1px solid black',
  },
  ttableRowCellDescription: {
    fontWeight: 'normal',
    border: '1px solid black',
    overflow: 'hidden',
    textOverflow: 'elipsis',
  },
});

function TableRow(props) {
  const TableRowStyle = useStyles();

  return (
    <tr className={TableRowStyle.tableRow} onClick={props.showUserCard} >
      <th className={TableRowStyle.tableRowCell}>{props.number}</th>
      <th className={TableRowStyle.tableRowCell}>{props.name}</th>
      <th className={TableRowStyle.tableRowCell}>{props.lastName}</th>
      <th className={TableRowStyle.tableRowCell}>{props.email}</th>
      <th className={TableRowStyle.tableRowCell}>{props.phone}</th>
      <th className={TableRowStyle.tableRowCell}>{props.adress}</th>
    </tr>
  )
}

export default TableRow;